}

.form{
    flex: 9 1;
}


.contactFirst input{
    margin: 10px 20px;
}

.contactFirst input:nth-child(1),
.contactFirst input:nth-child(2){
    width: 25%;
    height: 40px;
    padding-left: 10px;
    border-radius: 5px;
    border: 1px solid rgb(124, 124, 124);
}

.contactSecond input{
    margin: 10px 20px;
    padding-left: 10px;
    width: 54%;
    height: 40px;
    border-radius: 5px;
    border: 1px solid rgb(124, 124, 124);
}

textarea{
    width: 54%;
    height: 100px;
    margin: 10px 20px;
    padding-left: 10px;
    padding-top: 5px;
    color: rgb(124, 124, 124);
    border-radius: 5px;
    border: 1px solid rgb(124, 124, 124);
}

.submitButton{
    margin: 10px 20px;
    padding: 8px 40px;
    border-radius: 10px;
    background-color: black;
    color: white;
    border: none;
}

@media only screen and (max-width: 992px) {
