.html{
    width: 90%;
    -webkit-animation: html 3s;
            animation: html 3s;
}

.css{
    width: 80%;
    -webkit-animation: css 3s;
            animation: css 3s;
}

.javascript{
    width: 50%;
    -webkit-animation: javascript 3s;
            animation: javascript 3s;
}

.python{
    width: 50%;
    -webkit-animation: python 3s;
            animation: python 3s;
}

.c{
    width: 60%;
    -webkit-animation: c 3s;
            animation: c 3s;
}

.java{
    width: 70%;
    -webkit-animation: java 3s;
            animation: java 3s;
}

